.sensitive-wrap {
  padding: 20px;
  width: 100vw;
  height: 100vh;
}
.sensitive-wrap > .left-wrap,
.sensitive-wrap > .right-wrap {
  height: 100%;
  padding: 20px;
  border-right: 1px solid #333;
}
.sensitive-wrap > .left-wrap .item,
.sensitive-wrap > .right-wrap .item {
  margin-bottom: 8px;
}
.sensitive-wrap > .left-wrap .item .label,
.sensitive-wrap > .right-wrap .item .label {
  text-align: right;
  padding-right: 8px;
  letter-spacing: 1px;
}
.sensitive-wrap > .left-wrap .item .label::after,
.sensitive-wrap > .right-wrap .item .label::after {
  content: ':';
  padding-left: 2px;
}
